@font-face {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/dm-sans-v11-latin-ext-regular.eot');
  /* IE9 Compat Modes */
  src: local(''),
    url('../fonts/dm-sans-v11-latin-ext-regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */
    url('../fonts/dm-sans-v11-latin-ext-regular.woff2') format('woff2'),
    /* Super Modern Browsers */
    url('../fonts/dm-sans-v11-latin-ext-regular.woff') format('woff'),
    /* Modern Browsers */
    url('../fonts/dm-sans-v11-latin-ext-regular.ttf') format('truetype'),
    /* Safari, Android, iOS */
    url('../fonts/dm-sans-v11-latin-ext-regular.svg#DMSans') format('svg'),
  /* Legacy iOS */
}

