*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --font-f__main: "DM Sans";
  --font-f__secondary: "Titan One";
  --font-w__main: 500;
  --font-s__main: 16px;
  --color__w: #fff;
  --color__b: #000;
  --color__accent-first: #d41443;
  --color__accent-second: #ffa5ba;
  --color__btn-hover-bg: #ecebeb;
  --color__btn-hover-bg2: rgba(212, 20, 67, .8);
  --color__btn-location-bg: #d41342;
  --color__line: #e1e1e1;
  --color__socials: #fea5bb;
  --color__mob-menu-line: #e18298;
  --color__mob-menu-bg: #e17992;
  --color__products-1: #ffb8ca;
  --color__products-2: #f0d1a5;
  --color__products-3: #c2e297;
  --color__rewiers-dots: #c7133e;
  --color__rewiers-dots-slick: #d9d9d9;
  --color__rewiers-desc-bg: #fafafa;
  --color__icon-home: #d31342;
  --color__contact: #907e82;
  --color__footer-bg: #fff5f6;
  --flex__jc: center;
  --section__padding: 60px;
  --border-radius__main: 24px;
  --items: 1;
  --gap: 20px;
  --main__transition: 500ms cubic-bezier(.4, 0, .2, 1);
  --transition__scroll: 1000ms cubic-bezier(.4, 0, .2, 1);
  --transition__hover: 500ms cubic-bezier(.4, 0, .2, 1);
}

.products__list {
  gap: var(--gap);
  flex-wrap: wrap;
  display: flex;
}

.products__item {
  flex-basis: calc((100% - var(--gap) * (var(--items)  - 1) ) / var(--items) );
}

.gallery__list {
  opacity: 0;
  transition: opacity var(--transition__scroll), transform var(--transition__scroll);
  transform: scale(.9);
}

.gallery__list._active {
  opacity: 1;
  transform: scale(1);
}

.gallery__img:hover {
  transition: all var(--transition__hover);
  transform: scale(1.06);
}

.button {
  align-items: center;
  justify-content: var(--flex__jc);
  display: flex;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

ol, ul {
  list-style: none;
}

a {
  text-decoration: none;
}

:active, :focus {
  outline: 0;
}

