body {
  font-family: var(--font-f__main), sans-serif;
  font-weight: var(--font-w__main);
  font-size: var(--font-s__main);
}

body.modal-open, body.menu-mobile-open {
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a, button {
  cursor: pointer;
}

.section {
  padding-top: var(--section__padding);
  padding-bottom: var(--section__padding);
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
