.button {
  height: 44px;
  border-radius: 22px;
}

.button--white {
  color: var(--color__accent-first);
  background-color: var(--color__w);
}

.button--white:hover, .button--white:focus {
  background-color: var(--color__btn-hover-bg);
}

.button--accent {
  color: var(--color__w);
  background-color: var(--color__accent-first);
}

.button--accent:hover, .button--accent:focus {
  background-color: var(--color__btn-hover-bg2);
}

.upward {
  width: 65px;
  height: 65px;
  background-color: var(--color__accent-second);
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition: var(--main__transition);
  border-radius: 50%;
  position: fixed;
  bottom: 600px;
  right: 35px;
}

@media screen and (min-width: 1410px) {
  .upward {
    right: calc(50% - 670px);
  }
}

.btn__upward {
  fill: var(--color__accent-first);
  fill: var(--color__w);
  opacity: .8;
  transform: rotate(-90deg);
}

.upward.active {
  visibility: visible;
  opacity: .8;
  bottom: 35px;
}

.upward.active:hover {
  opacity: 1;
  transform: scale(1.1);
}

.menu-mobile {
  z-index: 10;
  width: 250px;
  height: 100vh;
  background-color: var(--color__mob-menu-bg);
  filter: drop-shadow(-8px 0 20px rgba(0, 0, 0, .25));
  transition: var(--main__transition);
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(150%);
}

.menu-mobile.is-open {
  transform: translateX(0);
}

@media screen and (min-width: 768px) {
  .menu-mobile {
    width: 254px;
  }
}

@media screen and (min-width: 1200px) {
  .menu-mobile {
    display: none;
  }
}

.menu-mobile .btn-menu {
