  height: 14px;
  margin-left: auto;
}

@media screen and (min-width: 768px) {
  .menu-mobile .btn-menu {
    margin-right: 12px;
  }
}

.menu-mobile .nav {
  margin-right: auto;
  display: flex;
}

.menu-mobile .nav__list {
  flex-direction: column;
