.backdrop {
  width: 100%;
  height: 100%;
  transition: transform var(--main__transition), opacity var(--main__transition), visibility var(--main__transition);
  opacity: 1;
  z-index: 10;
  background-color: rgba(0, 0, 0, .3);
  padding: 90px;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

@media screen and (max-width: 767px) {
  .backdrop {
    padding: 90px 16px;
  }
}

.modal {
  width: 100%;
  max-width: 480px;
  min-height: 480px;
  border-radius: var(--border-radius__main);
  background-color: var(--color__footer-bg);
  transition: transform var(--main__transition);
  margin-left: auto;
  margin-right: auto;
  padding: 40px 16px 16px;
  display: flex;
  transform: scaleX(1);
}

@media screen and (min-width: 480px) {
  .modal {
    width: 480px;
    padding: 44px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 600px;
  }
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scaleX(1.2);
}

.modal-btn__open {
  color: var(--color__w);
  background-color: var(--color__accent-first);
  box-shadow: 0px 4px 4px var(--color__b);
  transition: background-color var(--main__transition);
  border-width: 0;
  border-radius: 4px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 32px;
  font-family: inherit;
  display: block;
}

.modal-btn__open:hover, .modal-btn__open:focus {
  background-color: var(--color__accent-second);
}

.modal-btn__close {
  z-index: 1;
  width: 28px;
  height: 28px;
  border: 1px solid var(--color__b);
  background: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.modal__title {
  padding-bottom: 16px;
  font-size: 32px;
}

.modal__input {
  width: 100%;
  height: 40px;
  border: 1px solid var(--color__line);
  border-radius: 4px;
  padding-left: 40px;
}

.modal__input:focus, .modal-coment:focus {
  border: 1px solid var(--color__accent-second);
  transition: var(--main__transition);
}

.modal__input:focus + .label__icons {
  fill: var(--accent-color);
  transition: var(--main__transition);
}

.modal__input:focus ~ .label__icons {
  fill: #b5101b;
}

.modal__label {
  letter-spacing: .01em;
  color: var(--color__contact);
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.modal__label:not(:last-child) {
