  display: flex;
}

.modal__label-wrap {
  flex-direction: column;
  gap: 4px;
  margin-top: 10px;
  display: flex;
  position: relative;
}

.modal__label-wrap .label__icons {
  width: 12px;
  height: 12px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 8px;
  left: 14px;
  transform: translateY(-50%);
}

.modal-coment::-ms-input-placeholder {
